import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject'
import { ActiveAlertService } from '../../../services/active-alert-services'
import {AlertConfigService} from '../../../../../main/services/alert-config-service';
import { ActiveAlertData } from '../../../commons/active-alert';
import {CommonServices} from '../../../services/common-services';
import { ALERT_NAV_COMPONENT } from '../../../constants/alert-nav-constant';
import { Logger } from 'angular2-logger/core';
import * as CONS from '../../../constants/alert-constant';
import { FilertProperty } from "../../../interfaces/active-alert";

/* This is main component of active alert which handle all the model and the data flow in active alert */
@Component({
  selector: 'app-active-alert-main',
  templateUrl: './active-alert-main.component.html',
  styleUrls: ['./active-alert-main.component.css'],
  //providers:[ActiveAlertService]
})

/*Calss is used to handle main component*/  
export class ActiveAlertMainComponent implements OnInit 
{
  //This is used to stroe clas name
  className: string;
  //Main data variable which is use to handle active alert data
  alertData: ActiveAlertData;

  //This will holds alert type whether capacity, behavior or all
  alertType: string;          

  alertSeverity: number;  
  //used to show and hide alert filters in alert table  
  isShowFilter: boolean;

  //used to display message in gui
  msg: any;
   private _alertCounterClicked = new Subject<any>();  
  
  //This is used to stored filtered value for download
  downloadfilteredValue: ActiveAlertData  =  new ActiveAlertData();

  //hold the flag for access controll
  isAccessPermission: boolean;
  
  constructor(private activeAlertService: ActiveAlertService,private _commonServices:CommonServices, private _alertConfigService:AlertConfigService, private logger: Logger) 
  { 
    this.className = "ActiveAlertMainComponent";
    this.alertData = new ActiveAlertData();
   
    //initializing message variable
    this.msg = this.activeAlertService.messages;
  }

  /*This method is used to get data from server before DOM is load */
  ngOnInit() 
  {
    this._alertConfigService.alertCounterClicked$.subscribe(
        response =>
        {
          this.activeAlertService.$alertType = response.type;
          this.activeAlertService.$activeSeverityType = response.severity;
          var severity = CONS.ALERT_SEVERITY_NUM.ALL_SEVERITY;     
          if (response.severity == CONS.ALERT_SEVERITY_STRING.CRITICAL)
            severity = CONS.ALERT_SEVERITY_NUM.CRITICAL;
          else if (response.severity == CONS.ALERT_SEVERITY_STRING.MAJOR)
            severity = CONS.ALERT_SEVERITY_NUM.MAJOR;
          else if (response.severity == CONS.ALERT_SEVERITY_STRING.MINOR)
            severity = CONS.ALERT_SEVERITY_NUM.MINOR;
          
          this.activeAlertService.getAlertData(response.type, severity).subscribe(
            res => 
            {
              this.setAlertData(res);
              this.alertType = response.type;
              this.alertSeverity = severity;
              this._commonServices.changeRoutingItemActiveState('active');
            },
            error => this.logger.error(this.className, "ngOnInit", "Error in getting data from server."),
            () => this.logger.info(this.className, "ngOnInit", "Request Completed.")
          );
        }
      );

    try
    {
      let type = this.activeAlertService.$alertType;
      if (type != null && type !== "")
        this.alertType = type;
      else
        this.alertType = CONS.ALERT_TYPE_STRING.ALL;

      this.alertSeverity = CONS.ALERT_SEVERITY_NUM.ALL_SEVERITY;

      let severity = this.activeAlertService.$activeSeverityType;
      if (severity != null)
      {
        if (severity === CONS.ALERT_SEVERITY_STRING.CRITICAL)
          this.alertSeverity = CONS.ALERT_SEVERITY_NUM.CRITICAL;
        if (severity === CONS.ALERT_SEVERITY_STRING.MAJOR)
          this.alertSeverity = CONS.ALERT_SEVERITY_NUM.MAJOR;
        if (severity === CONS.ALERT_SEVERITY_STRING.MINOR)
          this.alertSeverity = CONS.ALERT_SEVERITY_NUM.MINOR;
      }

      this.logger.info(this.className, "ngOnInit" + this.alertType + ", severity = " + this.alertSeverity);
    
      //getting data for all alerts from server and call 
      this.activeAlertService.getAlertData(this.alertType, this.alertSeverity).subscribe(
        res => this.setAlertData(res),
        error => this.logger.error(this.className, "ngOnInit", "Error in getting data from server."),
        () => this.logger.info(this.className, "ngOnInit", "Request Completed.")
      );
    }
    catch (e)
    { 
      this.logger.error(this.className, "ngOnInit", e);
    }

    let accessControl = this._commonServices.getAccessControlData(CONS.ALERT_ACCESS_CONTROL.MODULE, CONS.ALERT_ACCESS_CONTROL.ACTIVE_ALERT).subscribe(
      res=>
      {
        this.setAccessData(res);
      },
      ()=>
      {
        accessControl.unsubscribe();
      }   
    );
    
  }

  /*Method is used to get data from server whenever there is change in alert type*/  
  changeAlertType(alertType: string)
  { 
    this.logger.info(this.className, "changeAlertType", "alert type = " + alertType + ", alertSeverity = " + this.alertSeverity);

    //setting alert type    
    this.alertType = alertType;

    //getting data from server for selected alert type and also set data in table
    this.activeAlertService.getAlertData(alertType, this.alertSeverity).subscribe(
      res => this.setAlertData(res),
      error => this.logger.error(this.className, "changeAlertType", "error in getting data from server")
    );
  }

  /*Method is used to get filtered data from server*/  
  getFilteredData(filterProperty: FilertProperty)
  {
    try
    {
      this.logger.info(this.className, "getFilteredData", "alertType = " + filterProperty.alertType + ", severity =" + filterProperty.severity);

      this.alertSeverity = filterProperty.severity;

      //setting alert type
      this.alertType = filterProperty.alertType;

      //getting data from server for selected alert type and also set data in table
      this.activeAlertService.getAlertData(filterProperty.alertType, filterProperty.severity).subscribe(
        res => this.setAlertData(res),
        error => this.logger.error(this.className, "getFilteredData", "error in getting data from server")
      );
    }
    catch (e)
    { 
      this.logger.error(this.className, "getFilteredData", e);
    }
  }
  
  showHideFilter(isShowFilter)
  {
    this.isShowFilter = isShowFilter;
  }

  /*This is used to set alert data after deserialize the original data got from server and set it to alertData variable for future use*/ 
  setAlertData(res) 
  {
    try
    {
      this.logger.debug(this.className, "setAlertData", " response = " + res);

      //Setting alert data after deserializeing response data
      this.alertData = this.activeAlertService.$commonService.convertToActiveAlertData(res);
      
      this.downloadfilteredValue.arrAlertData = this.alertData.arrAlertData;
      //displaying message
      this.activeAlertService.showMessage('success', 'Success', 'Active alert(s) fetched successfully');

      //reset selected records on table after setting alert data
      this.activeAlertService.setSelectedRowData([]);
    }
    catch (e)
    { 
      this.logger.error(this.className, "setAlertData", e);
    }
  }
  downloadFilterEvents(downloadfilteredValue: ActiveAlertData)
  {
    this.downloadfilteredValue = downloadfilteredValue;
  } 

  /**This is used to set alert access privilages setting data after deserialize the original data got from server and set it to alertAccessPrivilages variable for future use*/ 
  setAccessData(res) 
  {
    try
    {
      this.logger.info(this.className, "setAccessData", " response = " + JSON.stringify(res));
      if(res === null || res.All === undefined ||res.All === CONS.ACCESS_CONTROL_MODE.NO_PERMISSION || res.All === CONS.ACCESS_CONTROL_MODE.READ_MODE)
        this.isAccessPermission = true;
      else
        this.isAccessPermission = false; 
    }
    catch (e)
    { 
      this.logger.error(this.className, "setAccessData", e);
    }
  } 
}




