/** This class is used to store extension data */
export class ExtensionSettings{
  
    /** holds alert extension flage */
    onExt: boolean;
    
    /** holds array of alert all extension name */
    extNames: string[] = [];
    
    /** holds array of selected alert extension name */
    sExtNames: string[] = [];

    constructor()
    {

    }

}

/** This class is used to store policy data */
export class PolicySettings
{
    /** holds policy flage */
    onPolicy: boolean;

    /** holds alert mail flage */
    mail: boolean;

    /** holds alert sms flage */
    sms: boolean;

    /** holds alert snmp flage */
    snmp: boolean;

    /** holds alert thread demp flage */
    threadDump: boolean;
    
    /** holds alert heap dump flage */
    heapDump: boolean;

    /** holds alert tcp dump flage */
    tcpDump: boolean;

    /** holds alert cpu profile flage */
    cpuProfile: boolean;

    /** holds alert runScript flage */
    runScript: boolean;

    /** holds all information of alert extention */
    extension: ExtensionSettings;

    constructor()
    {
        this.extension = void 0;;
    }
}

/** This class is used to store alert setting data */
export class AlertSettings
{
    /** holds alert flage which enable/disable */
    alert: boolean;

    /** holds alert capacity flage */
    capacity: boolean;

    /** holds alert behavior flage */
    behaviour: boolean;
    
    /**  holds alert clear existing alert flage */
    clrExistingAlert: boolean;
    
    /** holds alert maintenance flage */
    maintenance: boolean;
    
    /** holds alert clear alert on maintenance flage */
    clrAlertOnMain: boolean;
    
    /**  holds clear external alert flage*/
    clrExtAlertOnInterval: boolean;
    
    clrExtAlertInt: number = 30; 

    /** holds debug level for setting */
    debugLevel: number = 0;

    profilingLevel: number = 0;
    
    /** holds view mode of baseline data */
    baselineViewMode: number = 0;

    /** holds min required baseline value */
    minBehabiorValueReq: number = 0.0;

    /**  its holds applied status of alert setting*/
    status: string =  "Error";

    /**  its holds applied message of alert setting*/
    msg: string  = "";

    /** its holds userName who is logging */
    userName : string = "Netstorm";
    
    /** its holds clear capacity alert falge */
    clrCapacityAlert: boolean;
    
    /** its holds clear behavior alert falge */
    clrBehaviorAlert: boolean;

    /** its holds external alert flage */
    externalAlert: boolean;
    
    /** is holds clear external alert flage */
    clrExternalAlert: boolean;

    /** holds all information of setting policy data */
    policy: PolicySettings;

    constructor()
    {
        this.policy = void 0;
    }
      
}


