import {Injectable} from "@angular/core";
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { Logger } from 'angular2-logger/core';
import { AlertData} from '../commons/active-alert';
import { Message } from 'primeng/primeng';
import { CommonServices } from "../services/common-services";

@Injectable()
export class ActiveAlertService
{
  //used to store class name
  className = "ActiveAlertService";

  //this will holds the message to display  
  messages: Message[] = [];

  //this is used to load active alert gui for specific alert type  
  private alertType: string;  

  //used for alert severity and setting at initial time  
  private activeSeverityType: string;
  
  //This is used to store active alert graph data
  activeAlertGraphData: any;

  //This is custom event. used to send selected record data to parent component  
  selectedRowData: AlertData[];

  //constructor
  constructor(private _http: Http, private logger: Logger, private commonServices: CommonServices) { }
  
  public get $alertType(): string
  { 
    return this.alertType;
  }
  public set $alertType(value: string)
  { 
    this.alertType = value;
  }
  
  public get $activeSeverityType(): string
  {
    return this.activeSeverityType;
  }
  public set $activeSeverityType(value: string)
  {
    this.activeSeverityType = value;
  }
  
  setSelectedRowData(alertData: AlertData[])
  { 
    this.selectedRowData = alertData;
  }

  /*Method is used to get active alert information*/
  getAlertData(alertType: string, alertSeverity: number)
  { 
    this.logger.info("Going to get data for  this.commonServices.getHostUrl() = ", this.commonServices.getHostUrl(), " alertType = ", alertType, "alertSeverity" , alertSeverity);
    
    return this._http.get(this.commonServices.getHostUrl() + "/DashboardServer/web/AlertDataService/getActiveAlerts?testRun=" + this.commonServices.$getTRNum + "&type=" + alertType + "&severity=" + alertSeverity)
      .map(res => res.json());
  }

  /** This Method is used to set url for show active alert graph data  in dashboard*/
  getAlertGraphData(queryParam: string)
  { 
    this.logger.debug(this.className, " getAlertGraphData ", "queryParam = ", queryParam);
    if(this.commonServices.$clientConnectionKey == "" || this.commonServices.$clientConnectionKey == null || this.commonServices.$clientConnectionKey == undefined)
    {
      this.showMessage('error', 'Error', 'Please open webdashboard and then click on show graph to open graphs');
      return;
    }
    let url = this.commonServices.getHostUrl() + "/DashboardServer/web/AlertDataService/alertHistoryGraphData?testRun=" + this.commonServices.$getTRNum + "&client_connection_key=" + this.commonServices.$clientConnectionKey + queryParam + "&preset=" + null + "&includeToday="+ 0;
    this.commonServices.$_alertConfig.setAlertGraphsData(url);
  }

  /* This method is used to force clear the active alert */  
  forceClear(alertIdObj: any, alertType: string, alertSeverity: number)
  {
    this.logger.info(this.className, "forceClear", "alerts to be force cleared = " + alertIdObj);
    return this._http.get(this.commonServices.getHostUrl() + "/DashboardServer/web/AlertDataService/forceClear?testRun=" + this.commonServices.$getTRNum + "&client_connection_key=" + this.commonServices.$clientConnectionKey + "&alertIds=" + alertIdObj + "&alertType=" + alertType + "&user=" + this.commonServices.$loginUser + "&severity=" + alertSeverity).map(res => res.json());
  }

  downloadData(fileFormat: string, activeAlertData: any, selectedSeverity: string)
  {

    let productType = this.$commonService.$productName;
    let object =
      {
        callFrom: "activeAlert",
        testRun: this.commonServices.$getTRNum,
        activeAlertData: JSON.stringify(activeAlertData),
        fileFormat: fileFormat,
        selectedSeverity: selectedSeverity,
        productType: productType
      };
    this.logger.info(this.className, "downloadData", "active alert data to be downloaded in : " + fileFormat + " format. & selectedSeverity = " + selectedSeverity + ", productType = " + productType);
    this._http.post(this.commonServices.$_cavConfigService.getURLByActiveDC() + "DashboardServer/web/AlertDataService/downloadAlertData", JSON.stringify(object))
     .map(res => res["_body"])
     .subscribe(
      res => this.openDownloadedFile(res),
      error => this.logger.error(this.className, "downloadData", "Error in downloading active alert data." + error)
    );
  }

  openDownloadedFile(res)
  {
    this.logger.info(this.className, "openDownloadedFile", "File is downloaded with name = " + res);
    window.open(this.commonServices.$_cavConfigService.getURLByActiveDC() + "/common/" + res);        
  }

  //this will print error message if any error comming on request
  private handleError(error: any): Promise<any> 
  {
    this.logger.error("Error occured during getting data from server - ", error);
    return Promise.reject(error.message || error);
  }
  
   //Method is used to show message popup  
  showMessage(strSeverity: string, strSummary: string, strDetail: string)
  {
    this.messages.splice(0);
    let mess = { severity: strSeverity, summary: strSummary, detail: strDetail }
    this.messages.push(mess);
    setTimeout(() => {
    this.messages.splice(this.messages.indexOf(mess), 1);
    }, 5000)    
  }

  public get $commonService(): CommonServices
  { 
    return this.commonServices;
  }
}



