/* This class is used to store all the filters information */
export class HistoryFilters
{
  //this used for operation type 0 - getData, 1- delete record
  operation: number;

  //used for status type opened:1 , Resolved:2
  state: number;

  //used to store alert type Capacity:0, Behavior:1, ALL:2
  alertType: number;

  //This stores the selected rule names
  ruleName: string;
  
  //This stores the selected key to permanently delete record from history database
  selectedKeyToDelete: string;

  //used to store Tier name or Topology Level 0 value
  tier: string = "-";

  //used to store Server name or Topology Level 1 value
  server: string = "-";

  //used to store Instance name or Topology Level 2 value
  instance: string = "-";

  //used to store field serial number which is defined in constraints
  strFilterFieldNum: number = -1;

  //Stores applied Filter String
  appliedStrFilter: string = "-";

  //This stores the testRun for which
  testRun: string;

  //this will stores the max number of record that is requested from Database
  limit: number;

  //used to check from which records data is needed. default is 0
  offset: number;

  //Used to store the applied preset to get data
  preset: string;

  //used to check where to include current day data or not  
  incCurData: number;  

  //Used to check whether the custom preset is applied or not. For custom:2 and for rest:1
  lastTimeStamp: number;

  //These are used for custom preset
  //Used to store date and time from where the data is required
  fromTimeStamp: string;

  //Used to store date and time till data is required
  toTimeStamp: string;

  //Not in used
  timeStamp: number = -1;
  
  //This will holds all the filter information for Alert Severity, Alert Type, Advance
  mapTypeStatusWithFilter: any;

  //This stores the selected alertId to show all record from history database 
  selectedAlertId: string;
}

//This is used to store Alert current and previous severity and it is used in mapTypeStatusWithFilter of HistoryFilters class
export class AlertSeverityFilter
{
  severity: number = -1;
  prevSeverity: number = -1;
}



