import { Component, OnInit, Input, Output, EventEmitter }  from '@angular/core';
import { IMAGE_PATH } from "../../../constants/alert-constant";
import { ActiveAlertData, AlertData} from '../../../commons/active-alert';
import { Logger } from 'angular2-logger/core';
import { ActiveAlertService } from '../../../services/active-alert-services';
import { ColFilterProperty, ValueFlageProperty } from '../../../interfaces/active-alert';
import { CommonServices } from '../../../services/common-services';
import * as CONS from '../../../constants/alert-constant';
import * as moment from 'moment';

/*This component is used to create Table panel and also handle all the operation performed on table */
@Component({
  selector: 'app-table-pane',
  templateUrl: './table-pane.component.html',
  //styleUrls: ['./table-pane.component.css'],
  //inputs: ['alertData'],
  providers: [ActiveAlertData]
})

/*This class will handle all the operation performed in table panel*/  
export class TablePaneComponent implements OnInit
{
  //used to store class name
  className: string;

  //This is used to hold alert icon path
  imagePath: string;

  //this will store all the alert data that is available on server  
  @Input()
  alertData: ActiveAlertData;
  
  //This will holds selected alert type
  @Input()
  alertType: string;
 
  //used to show and hide alert filter in alert table 
  @Input() isShowFilter: boolean;
  rowNum = 0;

  //hold the flag for access controll
  @Input()
  isAccessPermission: boolean;
  
  //Thes are used to changed table header for vectors
  tier: string;
  server: string;
  instance: string;
 
  //this is Show MultiDc in Case of ALL
  dc: any;
  
  numRowPerPage: number;
  arrRowsPerPage: number[];

  //This is height of table pane
  tablePanelHeight: string;

  columnHide:ColFilterProperty[];
  selectedColumnHide: ValueFlageProperty[] = [];
  consColHide:ColFilterProperty[];

  
  
  @Output()
  downloadFilterEvent = new EventEmitter<ActiveAlertData>();

  //constructor
  constructor(private logger: Logger, public activeAlertService: ActiveAlertService, public commonService: CommonServices) 
  {
    this.dc = activeAlertService.$commonService.getDcName();
    this.numRowPerPage = 15;
    this.arrRowsPerPage = [15, 20, 50, 100, 200];
    this.tablePanelHeight = (window.innerHeight - 275) + "px";
  }

  ngOnInit()
  {
    //setting class name into variable
    this.className = "TablePaneComponent";

    //setting alert icons path
    this.imagePath = IMAGE_PATH;

    this.tier = "Tier";
    this.server = "Server";
    this.instance = "Instance";
    if (this.activeAlertService.$commonService.$productName === "netvision")
    {
      this.tier = CONS.COL_INDICES + "1";
      this.server = CONS.COL_INDICES + "2";
      this.instance = CONS.COL_INDICES + "3";
    }
    this.commonService.alertType$.subscribe(
      res => {
        this.alertType = res.aTypes,
           this.dc = res.dcName,
      this.showHideTableHeader()}
    );
    this.showHideTableHeader();
  }
  
  showHideTableHeader()
  {
    this.consColHide= [];
    this.selectedColumnHide = [];
    this.consColHide = CONS.COL_FILTER_PROPERTY;
    this.columnHide = [];
    if(this.alertType == "Behavior")
      this.consColHide[4].value.value = "Behavior";
    else if(this.alertType == "Capacity")
    {
      this.consColHide[4].value.value = "Capacity";
      this.consColHide[17].value.value = "Capacity";
    }
    if(this.dc !== "ALL")
      this.consColHide[3].value.value = "";
    else
      this.consColHide[3].value.value = "ALL";
    for(let i = 0; i < CONS.COL_FILTER_PROPERTY.length; i++) {
      if(this.consColHide[i].value.value == "status" || this.consColHide[i].value.value == this.alertType || this.consColHide[i].value.value === "")
        continue;
      else
      {
        this.selectedColumnHide.push(this.consColHide[i].value);
        this.columnHide.push(CONS.COL_FILTER_PROPERTY[i]);
      }
    }
    
    this.selectedColumnHide.splice(2,1);
    for(let i = 0; i < this.consColHide.length; i++)
      this.consColHide[i].value.flage = false;
    this.consColHide[2].value.flage = true;
  }


  //This method is used to get graph data from server  
  showGraph(alertData: AlertData)
  { 
    try
    {
      let queryParam: string;
      let severity: number;
      let graphKey: string;
      let keyFormater = "_";
      let keySaparator = ";";
      let querySaparator = "&";
      
      let startTime: number = new Date().getTime();
      let endTime = 0;

      //If there is no record selected
      if (alertData === undefined)
      {
        this.activeAlertService.showMessage('error', 'Error', 'No row is selected. Select record to show graph(s).');
        this.logger.error(this.className, "showGraph", "No row is selected. Select record to show graph(s).");
        return;
      }
      if(alertData.groupId == -1 || alertData.groupId == 0 || alertData.graphId == -1 || alertData.graphId == 0)
      {
        this.activeAlertService.showMessage('error', 'Error', 'Open Alert graph is not applicable for derived graph(s).');
        this.logger.error(this.className, "showGraph", "Open Alert graph is not applicable for derived graph(s).");
        return; 
      }
   
      let date = new Date(alertData.alertTime);
      startTime = date.getTime();
      endTime = date.getTime();
    
      if (alertData.severity === CONS.ALERT_SEVERITY_STRING.CRITICAL)
        severity = CONS.ALERT_SEVERITY_NUM.CRITICAL;
      else if (alertData.severity === CONS.ALERT_SEVERITY_STRING.MAJOR)
        severity = CONS.ALERT_SEVERITY_NUM.MAJOR;
      else if (alertData.severity === CONS.ALERT_SEVERITY_STRING.MINOR)
        severity = CONS.ALERT_SEVERITY_NUM.MINOR;
      else if (alertData.severity === CONS.ALERT_SEVERITY_STRING.NORMAL)
        severity = CONS.ALERT_SEVERITY_NUM.NORMAL;
      if(this.dc == 'ALL')
        graphKey = "graphKey=" + severity + keyFormater + alertData.groupId + keyFormater + alertData.graphId + keyFormater + alertData.tier + keyFormater + alertData.server + keyFormater + alertData.instance+keyFormater+alertData.dc;
      else
        graphKey = "graphKey=" + severity + keyFormater + alertData.groupId + keyFormater + alertData.graphId + keyFormater + alertData.tier + keyFormater + alertData.server + keyFormater + alertData.instance
      
      let fromDate = new Date(startTime - CONS.TIME_IN_MILIS.MINUTE_30);
      let toDate = new Date(endTime + CONS.TIME_IN_MILIS.MINUTE_30);

      queryParam = querySaparator + "fromDate=" + moment(fromDate).format('MM/DD/YYYY HH:mm:ss') + querySaparator + "toDate=" + moment(toDate).format('MM/DD/YYYY HH:mm:ss') + querySaparator + graphKey;

      this.activeAlertService.getAlertGraphData(queryParam);
    }
    catch (e)
    { 
      this.logger.error(this.className, "showGraph", e);
    }
  }
  
  //Custom Sorting on date and timecolumn
  mysort(event, activeAlertData)
  {
    let temp = event.field;
    if (event.order === -1)
    {
      event.order = 1;
    }
    else
    {
      event.order = -1;
    }
    
    let value1;
    let value2;
    activeAlertData = activeAlertData.sort(function (a, b)
    {
      if (temp == "timeAgo")//sorting of time stamp HH:MM:SS(timeAgo)
      {
        let temp1 = a[temp].split(":");
        let temp2 = b[temp].split(":");
        value1 = 0;
        value2 = 0;
        for (let i = 0; i < temp1.length; i++) 
        {
          value1 = value1 + parseInt(temp1[i]);
          value2 = value2 + parseInt(temp2[i]);
          if (value1 !== value2)
            break;
        }
      }
      else if (temp === "alertTime")//sorting of dateFormat(alertTime)
      {
        value1 = Date.parse(a[temp]);
        value2 = Date.parse(b[temp]);
      }
      else if(temp === "alertValue" || temp === "thresholdValue")//sorting of dateFormat(alertTime)
      {
        let tempA = JSON.parse(JSON.stringify(a[temp]));
        let tempB = JSON.parse(JSON.stringify(b[temp]));
        tempA = tempA.replace(",", "").replace("-","-2.0").replace("NaN","-1.0");
        tempB = tempB.replace(",", "").replace("-","-2.0").replace("NaN","-1.0");
        value1 = parseFloat(tempA);
        value2 = parseFloat(tempB);
      }

      if (event.order === -1)
      {
        return (value2 > value1) ? 1 : ((value2 < value1) ? -1 : 0);
      }
      else
      {
        return (value1 > value2) ? 1 : ((value1 < value2) ? -1 : 0);
      }
    });
    this.alertData.arrAlertData = [] ;
    activeAlertData.forEach(element =>
    {
      this.alertData.arrAlertData = this.alertData.arrAlertData.concat(element);
    });
  }

  /** This method will holds filter data of active alert data*/
  onFilterData(event)
  {
    let activeAlertFilterData: ActiveAlertData  = new ActiveAlertData();
    activeAlertFilterData.arrAlertData = event.filteredValue;
    this.downloadFilterEvent.emit(activeAlertFilterData);
  }

  callOnSelectedColumnHideType(event)
  {
    for(let i = 0; i < this.consColHide.length; i++)
      this.consColHide[i].value.flage = true;
    for(let j = 0; j < event.value.length; j++)
    {
      for(let i = 0; i < this.consColHide.length; i++)
      {
        if(event.value[j].value === this.consColHide[i].value.value)
          this.consColHide[i].value.flage = false;
      }
    }
  }
}



