import { Component, Input, ViewContainerRef } from '@angular/core';
import { ConditionData } from './condition-data';
import { AlertRuleService } from '../../../services/alert-rule-service';
@Component({
  template: `
<p-panel [style]="{'margin':'5px 2px 5px 0px'}" [collapsed]=iscollapsedPanel>
  <p-header>
    <div style="float:left">
      <span ><input type="text" size="30" pInputText [(ngModel)]="data.name" placeholder="{{data.name}}"> </span>
      <span>{{conditionPanelHeader}}</span>
    </div>
    <div style="float:right">
      <span *ngIf="iscollapsedPanel" style="cursor: pointer" title="Open" (click) = "collapsedPanel(data.id)"><i class="fa fa-chevron-right" style="font-size: 1.2em;"></i></span>
      <span *ngIf="!iscollapsedPanel" style="cursor: pointer" title="Close" (click) = "collapsedPanel(data.id)"><i class="fa fa-chevron-down" style="font-size: 1.2em;"></i></span>
      <span style="cursor: pointer" title="Delete" (click) = "deleteCondition(data.id)"><i class="fa fa-times" style="font-size: 1.4em;"></i></span>
    </div>
  </p-header>
  <div class=" ui-g">
    <div class="ui-g-12 ui-md-6 ui-lg-4">
      <div class="labels">Matric Group</div>
      <p-dropdown   [options]="alertRuleService.matricGroup" [(ngModel)]="data.groupName"  [style]="{'width':'16em'}" title="{{data.groupName}}"></p-dropdown>
    </div>
    <div class="ui-g-12 ui-md-6 ui-lg-8">
      <div class="labels">Matric Name</div>
      <p-dropdown [options]="alertRuleService.matricName" [(ngModel)]="data.graphName"  [style]="{'width':'16em'}" title="{{data.graphName}}"></p-dropdown>
    </div>
  </div>
  <div class=" ui-g">
    <div class="ui-g-12 ui-md-6 ui-lg-12">
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <div class="labels">having</div>
        <p-dropdown [options]="alertRuleService.dataType" [(ngModel)]="data.dataType"  [style]="{'width':'14em'}" title="{{data.dataType}}"></p-dropdown>
      </div>
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <div class="labels">value</div> 
        <p-dropdown [options]="alertRuleService.operator" [(ngModel)]="data.operator"  [style]="{'width':'14em'}" title="{{data.operator}}"></p-dropdown>
      </div>
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <p-dropdown [options]="alertRuleService.baselineName" [(ngModel)]="data.baselineName"  [style]="{'width':'18em'}" title="{{data.baselineName}}"></p-dropdown>
      </div>
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <div class="labels">by</div> 
        <input  type="text" size="8" [(ngModel)]="data.threshold" pInputText title="Provide">
        <p-dropdown [options]="alertRuleService.conditionType" [(ngModel)]="data.conditionType"  [style]="{'width':'10em'}"></p-dropdown>
      </div>
    </div>
  </div>
  <div class=" ui-g">
    <div class="ui-g-12 ui-md-6 ui-lg-12">
      <div style="float: left;padding: 5px 0px 0px 5px;"><p-checkbox [(ngModel)]="data.minDiff" binary="true"></p-checkbox></div>
      <div style="float: left;padding: 5px 10px 0px 5px;">minimum change from baseline is</div> 
      <div><input  type="text" size="8" pInputText [(ngModel)]="data.minDiffValue"></div>
    </div>
  </div>
</p-panel>
  `
})

export class RuleConditionDefinition
{
  @Input() data: any;
  iscollapsedPanel: boolean = false;
  conditionPanelHeader: string = "";
  constructor(public alertRuleService: AlertRuleService) { 
    
  }
  
  collapsedPanel(Idx: number)
  {
    this.iscollapsedPanel = !this.iscollapsedPanel
    if(this.iscollapsedPanel)
      this.conditionPanelHeader = " Matric Group ";
    else
      this.conditionPanelHeader = "";
  }
  deleteCondition(conditionId)
  {
    let conditionIdx = conditionId;
    this.alertRuleService.conditionViewContainerRef.remove(conditionIdx);
    this.alertRuleService.arrRuleCondition.splice(conditionIdx, 1);
    this.alertRuleService.arrRuleCondition.forEach((element,index) => {
      this.alertRuleService.arrRuleCondition[index].data.id = index;
    });
  }

}



