import { Component, Input, ViewContainerRef } from '@angular/core';
import { ConditionData } from './condition-data';
import { AlertRuleService } from '../../../services/alert-rule-service';
import * as CONS from "../../../constants/alert-rule-constant";
@Component({
  template: `
<p-panel [collapsed]=iscollapsedPanel>
  <p-header>
    <div style="float:left">
      <span ><input type="text" size="30" pInputText [(ngModel)]="data.name" placeholder="{{data.name}}"> </span>
      <span>{{conditionPanelHeader}}</span>
    </div>
    <div style="float:right">
      <span *ngIf="iscollapsedPanel" style="cursor: pointer" title="Open" (click) = "collapsedPanel(data.id)"><i class="fa fa-chevron-right" style="font-size: 1.2em;"></i></span>
      <span *ngIf="!iscollapsedPanel" style="cursor: pointer" title="Close" (click) = "collapsedPanel(data.id)"><i class="fa fa-chevron-down" style="font-size: 1.2em;"></i></span>
      <span style="cursor: pointer" title="Delete" (click) = "deleteCondition(data.id, data.severityType)"><i class="fa fa-times" style="font-size: 1.4em;"></i></span>
    </div>
  </p-header>
  <div class=" ui-g">
    <div class="ui-g-12 ui-md-6 ui-lg-4">
      <div class="labels">Matric Group</div>
      <p-dropdown   [options]="alertRuleService.matricGroup" [(ngModel)]="data.groupName"  [style]="{'width':'16em'}" title="{{data.groupName}}"></p-dropdown>
    </div>
    <div class="ui-g-12 ui-md-6 ui-lg-8">
      <div class="labels">Matric Name</div>
      <p-dropdown [options]="alertRuleService.matricName" [(ngModel)]="data.matricName"  [style]="{'width':'16em'}" title="{{data.matricName}}"></p-dropdown>
    </div>
  </div>
  <div class=" ui-g">
    <div class="ui-g-12 ui-md-6 ui-lg-12">
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <div class="labels">having</div>
        <p-dropdown [options]="alertRuleService.dataType" [(ngModel)]="data.dataType"  [style]="{'width':'14em'}" title="{{data.dataType}}"></p-dropdown>
      </div>
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <div class="labels">value</div> 
        <p-dropdown [options]="alertRuleService.operator" [(ngModel)]="data.operator"  [style]="{'width':'14em'}" title="{{data.operator}}"></p-dropdown>
      </div>
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <p-dropdown [options]="alertRuleService.baselineName" [(ngModel)]="data.baselineName"  [style]="{'width':'18em'}" title="{{data.baselineName}}"></p-dropdown>
      </div>
      <div class="ui-g-12 ui-md-6 ui-lg-3">
        <div class="labels">by</div> 
        <input  type="text" size="8" [(ngModel)]="data.threshold" pInputText title="Provide">
        <p-dropdown [options]="alertRuleService.conditionType" [(ngModel)]="data.conditionType"  [style]="{'width':'10em'}" title="{{data.conditionType}}"></p-dropdown>
      </div>
    </div>
  </div>
  <div class=" ui-g">
    <div class="ui-g-12 ui-md-3 ui-lg-1" style="padding: 0.9em 0.5em;"> and indices </div>
    <div class="ui-g-12 ui-md-6 ui-lg-9"> <input pInputText style="width: 100%" type="text" [(ngModel)]="description" /></div>
    <div class="ui-g-12 ui-md-3 ui-lg-2" style="padding: 0.9em 0.5em;"> <span style="cursor: pointer" title="Select Indices" (click) = "delete()">Select Indices</span></div>
  </div>
  <div class=" ui-g">
    <div class="ui-g-12 ui-md-6 ui-lg-12">
      <div style="float: left;padding: 5px 0px 0px 5px;"><p-checkbox [(ngModel)]="data.minDiff" binary="true"></p-checkbox></div>
      <div style="float: left;padding: 5px 10px 0px 5px;">minimum change from baseline is</div> 
      <div><input  type="text" size="8" pInputText [(ngModel)]="data.minDiffValue" title="{{data.minDiffValue}}"></div>
    </div>
  </div>
</p-panel>
  `
})

export class RuleIndividualCondition
{
  @Input() data: any;
  iscollapsedPanel: boolean = false;
  conditionPanelHeader: string = "";
  
  constructor(private alertRuleService: AlertRuleService) { }
  
  collapsedPanel(Idx: number)
  {
    this.iscollapsedPanel = !this.iscollapsedPanel
    if(this.iscollapsedPanel)
      this.conditionPanelHeader = " Matric Group ";
    else
      this.conditionPanelHeader = "";
  }
  deleteCondition(conditionId: number, severityType: number)
  {
    switch (severityType) {
      case CONS.ALERT_RULE_CONST.SEVERITY_MINOR:
      {
        let minorIdx = conditionId;
        this.alertRuleService.minorViewContainerRef.remove(minorIdx);
        this.alertRuleService.arrMinorCondition.splice(minorIdx, 1);
        this.alertRuleService.arrMinorCondition.forEach((element,index) => {
          this.alertRuleService.arrMinorCondition[index].data.id = index;
        });
        break;
      }
      case CONS.ALERT_RULE_CONST.SEVERITY_MAJOR:
      {
        let majorId = conditionId;
        this.alertRuleService.majorViewContainerRef.remove(majorId);
        this.alertRuleService.arrMajorCondition.splice(majorId, 1);
        this.alertRuleService.arrMajorCondition.forEach((element,index) => {
          this.alertRuleService.arrMajorCondition[index].data.id = index;
        });
        break;
      }
      case CONS.ALERT_RULE_CONST.SEVERITY_CRITICAL:
      {
        let criticalId = conditionId;
        this.alertRuleService.criticalViewContainerRef.remove(criticalId);
        this.alertRuleService.arrCriticalCondition.splice(criticalId, 1);
        this.alertRuleService.arrCriticalCondition.forEach((element,index) => {
          this.alertRuleService.arrCriticalCondition[index].data.id = index;
        });
        break;
      }
      default:
        break;
    }
  }

}



