import { Component, ViewChild, OnInit } from '@angular/core';
import { HistoryServices } from '../../../services/alert-history-services';
import {MatSidenav} from '@angular/material';
@Component({
  selector: 'app-alert-history-main',
  templateUrl: './alert-history-main.component.html',
  styleUrls: ['./alert-history-main.component.css']
})
export class AlertHistoryMainComponent implements OnInit
{
  filterStatus: boolean;
  bottomPanHeight: number;
  @ViewChild('historyNavSideBar') menuSideNav: MatSidenav;

  constructor(public historyServices: HistoryServices)
  {
    this.filterStatus = false;
    this.bottomPanHeight = window.innerHeight - 102;
    historyServices.leftAndTablePanelheight = this.bottomPanHeight;
  }

  ngOnInit()
  {
    console.log(window.innerHeight, this.bottomPanHeight);
    //subscribe alertHistory service to apply row grouping in history table
    this.historyServices.showFilterPanel$.subscribe(
      res =>
      {
        if(res)
          this.menuSideNav.open();
        else
          this.menuSideNav.close();
      }
    );

  }

  filterDisplayStatus()
  {
      this.filterStatus = !this.filterStatus;  
  }
}



