import * as CONS from "../constants/alert-rule-constant";
export class SeverityConditionDTO
{
  operator: number = CONS.ALERT_RULE_CONST.RULE_OPERATOR_TYPE_ANY;
  arrConditions: RuleConditionsDTO[];
  constructor()
  {
    this.arrConditions = void 0;
  }
}

/** This class is used to store array of maintenance Setting data */
export class RuleConditionsDTO
{
  id: number = 0;
  severityType: number = CONS.ALERT_RULE_CONST.SEVERITY_ALL;
  name: string = "";
  groupName: string = "";
  graphName: string = "";
  dataType: number = CONS.ALERT_RULE_CONST.AVERAGE_DATA_TYPE;
  operator: number = CONS.ALERT_RULE_CONST.OPERATOR_ABSOLUTE_INCREASE;
  baselineName: string = "";
  threshold: number = CONS.ALERT_RULE_CONST.DEFAULT_INT_VAL;
  conditionType: number = CONS.ALERT_RULE_CONST.OPERATOR_DEFINITION_TYPE_PERCENTAGE;
  minDiff: boolean = false;
  minDiffValue: number = CONS.ALERT_RULE_CONST.DEFAULT_INT_VAL;
  vectorMode: number = 0;
  vectorName: string = "";
}

/** This class is used to store all alert maintenance setting data */
export class RuleDataDTO
{
  enable: boolean = true;
  name: string = "";
  threshType: number = CONS.ALERT_RULE_CONST.MOVING_AVERAGE_THRESH_TYPE;
  timeWindow: number = 5; //default moving time window is of 5 minutes
  checkStatus: boolean = true; //used to check for continous alert to log in alert history
  checkStatusTime: number = 30; //default checkstatus time will be of 30 minutes
  syncEnable: boolean = true; //default sync time is enavle. It used in fixed window
  syncTime: number  = -1; //It is only used at server to check the next sync time for rule
  checkDataPtType: number = CONS.ALERT_RULE_CONST.CHECK_DATA_POINT_TYPE_LAST; //used to decide whether any data point is satisfied or consecutive one
  pctCheckDataPoint: number = 100; //check data point is default 100 %
  level: number = CONS.ALERT_RULE_CONST.RULE_LEVEL_ON_TIER; //default rule level type i.e. tierLevel or individual level
  tiers: any = ""; //used for selected tier names if level is of tier type
  ruleOperator: number = CONS.ALERT_RULE_CONST.RULE_OPERATOR_TYPE_ANY; // default rule operator is of any type
  arrConditions:RuleConditionsDTO[];//used to store all the conditions for Tier type
  criticalCondition: SeverityConditionDTO; //used to store all the critical conditions for Individual type
  majorCondition: SeverityConditionDTO; //used to store all the critical conditions for Individual type
  minorCondition: SeverityConditionDTO; //used to store all the critical conditions for Individual type
  severityLevel: number = CONS.ALERT_RULE_CONST.SEVERIRTY_DECISION_ON_PERCENTAGE_OF_SERVER;// default severity decide on which level
  severity: number = 3;// default severity type for individual server is CRITICAL
  criticalThreshold: number = -1; //default critical threshold value is -1
  majorThreshold: number = -1; //default major threshold value is -1
  minorThreshold: number = -1; //default minor threshold value is -1
  constructor()
  {
    this.arrConditions = void 0;
  }
}

export class ConditionData
{
  data: any;
  constructor()
  {
      this.data = void 0;
  }
}


