import { Directive, Input } from '@angular/core';

@Directive({
  selector: '[limit-to]',
  host: {
    '(keypress)': 'onKeypress($event)',
  }
})
export class LimitToDirective {
  @Input('limit-to') maxDigit;

  onKeypress(e)
  {
    const limit = +this.maxDigit;
    if (e.target.value.length === limit)
      e.preventDefault();
  }
}



