import { NgModule} from '@angular/core';
import { Routes, RouterModule} from '@angular/router';
import { AppComponent } from '../app.component';
import { BottomPaneComponent } from './../components/active-alert/bottom-pane/bottom-pane.component';
import { ActiveAlertMainComponent } from './../components/active-alert/active-alert-main/active-alert-main.component';
import { AlertHistoryMainComponent } from './../components/alert-history/alert-history-main/alert-history-main.component';
import { AlertSettingComponent } from './../components/alert-setting/alert-setting.component';
import { AlertMaintenanceComponent } from './../components/alert-maintenance/alert-maintenance.component';
import { RuleDescriptionComponent } from './../components/alert-rule/rule-description/rule-description.component';
const ALERT_APP_ROUT: Routes = [
  { path: 'home/alert/active', component: ActiveAlertMainComponent },
      { path: 'home/alert/history', component: AlertHistoryMainComponent },
      { path: 'home/alert/setting', component: AlertSettingComponent },
      { path: 'home/alert/maintenance', component: AlertMaintenanceComponent },
      { path: 'home/alert/rule', component: RuleDescriptionComponent },
      { path: '', redirectTo: 'active', pathMatch: 'full' },
      { path: '**', component: ActiveAlertMainComponent }
];

@NgModule({
  imports: [
    RouterModule.forRoot(ALERT_APP_ROUT)
  ],
  exports: [
    RouterModule
  ]
})

export class AppRoutingModule{}


