  showFilter()
  {
    this.isShowFilter = !this.isShowFilter;
  }
  
  /** This method is used to show all future maintenance as well as past maintenance */
  showHistoricWindow(event)
  {
    let maintenanceSettings = new MaintenanceSettings();
    let maintenanceSettingsData = new MaintenanceSettingsData();

    if(this.isShowHistoricWindow === true)
    {
      maintenanceSettings.reqType = CONS.MAINTAINANCE_WINDOW_CONSTANT.GET_ALL_MAINTENANCE;
    }
    else
    {
      maintenanceSettings.reqType = CONS.MAINTAINANCE_WINDOW_CONSTANT.GET_ACTIVE_MAINTENANCE;
    }
    let tmpMSettingsData = [];
    tmpMSettingsData.push(maintenanceSettingsData);
    maintenanceSettings.arrMSetting = tmpMSettingsData;
    this.commonServices.showProgressBar("Getting Maintenance History data from server, Please wait...");
    this.alertMaintenanceService.getMaintenanceAlertData(maintenanceSettings).subscribe(
      res =>
      {
        this.setMaintenanceAlertData(res);
      },
      error =>
      {
        this.commonServices.isHideProgress = true;
        this.alertMaintenanceService.showMessage("error", "Error", error);
        this.logger.error(this.className, "addMaintenanceData", "Error in getting data from server.");
      });
  }
  
  /** This method is used to delete multiple record from maintenance table for effective scheduling*/
  deleteAndCreatMultipleMData(isAccessPermission: boolean)
  {
    if(isAccessPermission)
      return;
    let maintenanceSettings = new MaintenanceSettings();
    maintenanceSettings.reqType = CONS.MAINTAINANCE_WINDOW_CONSTANT.DELETE_MAINTENANCE_CONFIG_SETTING;

    let tmpMSettingsData = new Array<MaintenanceSettingsData>();
    this.selectedRowData.forEach(element => {
      if (element.deleteStatus != CONS.MAINTAINANCE_WINDOW_CONSTANT.PURGE_MAINTENANCE_PERIOD)
      {
        element.deleteStatus = CONS.MAINTAINANCE_WINDOW_CONSTANT.DELETE_AND_CREATE_DATA_FOR_MAINTENANCE_PERIOD;
        tmpMSettingsData.push(element);
      }
    });

    maintenanceSettings.arrMSetting = tmpMSettingsData;
    if (maintenanceSettings.arrMSetting.length <= 0)
    {
      this.selectedRowData = [];
      this.alertMaintenanceService.showMessage("error", "Error", "Please select at least one Record to delete.");
      return;
    }
    this.confirmationService.confirm({
      message: 'Do you want to delete the selected maintenance schedule and make the schedule in-effective? \n This option will regenerate the data for the deleted maintenance duration.',
      accept: () =>
      {
        this.commonServices.showProgressBar("Going to delete applied maintenance duration. Please wait...");
        this.alertMaintenanceService.applyMaintenanceSettings(maintenanceSettings).subscribe(
        res =>
        {
          this.setAddDeleteRecords(res);
        },
        error =>
        {
          this.commonServices.isHideProgress = true;
          this.alertMaintenanceService.showMessage("error", "Error", error);
          this.logger.error(this.className, "addMaintenanceData", "Error in getting data from server.");
        });
      },
      reject: () => {
        return;
      }
    });
  }

