  @ViewChild(TierConditionDirective) tierCondition: TierConditionDirective;
  @ViewChild(IndividualConditionDirective) individualCondition: IndividualConditionDirective;
  @ViewChild(IndividualMajorDirective) individualMajorDirective: IndividualMajorDirective;
  @ViewChild(IndividualMinorDirective) individualMinorDirective: IndividualMinorDirective;
  
  /** this is used to store all derived calculation expression for dropdown */
