import { Component, OnInit, Output, EventEmitter, ViewChild } from '@angular/core';
import { RuleDataDTO } from './../../../commons/rule-data';
import { AlertRuleService } from '../../../services/alert-rule-service';
import { Logger } from 'angular2-logger/core';
@Component({
  selector: 'app-rule-description',
  templateUrl: './rule-description.component.html',
  styleUrls: ['./rule-description.component.css']
})
export class RuleDescriptionComponent implements OnInit {
  className = "RuleDescriptionComponent";
  isOpenAddEditWindow: boolean = false;
  
  panelNumber: number = 0;
  @ViewChild('addCondition') panel;

  ruleDataDTO: any;

  constructor(private ruleService: AlertRuleService,private logger: Logger ) {
    this.ruleDataDTO =  new RuleDataDTO();
   }

  ngOnInit()
  {
    this.ruleService.getTierLevelData([])
  }
  
 

  addMutipleCondition()
  {
    this.panelNumber++;
    let data = this.panel.nativeElement;
    let da = this.panel;
    
    console.log("data ==>>", data, "--", da);
  }

  addEditRuleWindow()
  {
    this.isOpenAddEditWindow = true;

  }

  closeAddEditWindow(isOpenAddEditWindow: boolean)
  {
    this.isOpenAddEditWindow = isOpenAddEditWindow;
  }
}



