  //making url to get and post method
  getServiceURL(param: string): string
  {
    var activeDC = '';
    
    if(this._config.getINSAggrPrefix().indexOf('node') != -1 || this._config.getINSAggrPrefix().indexOf('tomcat') != -1)
      activeDC = this._config.getActiveDC();
    
    let url = this._config.getINSAggrPrefix() + activeDC + '/DashboardServer/web/AlertDataService/' + param;
  
    return url;	  
  }

   /**calling dialog box for showing error & success message */
   openMessageDialog(header: string, message: string)
   {
    this.isDisplayMsg = true;
    this.header = header;
    this.msg = message;
    return;
   }

   /**the method will be called at the time of saving data into the server */
   showProgressBar(progressMessage: string)
   {
     this.progressValue = 0;
     this.isShowProgressBar = true;
     /* initializing progressbar variables */
     this.dialogMessage = progressMessage;
     this.isHideProgress = false;
     let interval = setInterval(() =>
     {
       if (this.progressValue < 85)
       {
         this.progressValue = this.progressValue + Math.floor(Math.random() * 10) + 1;
         if (this.progressValue == 100)//in case progressValue is 100 then decrease it
           this.progressValue = 86;  
       }
 
       if (this.isHideProgress == true)
       {
         this.progressValue = 100;
         this.isShowProgressBar = false;
 
         clearInterval(interval); //to hide ProgressBar
       }
     }, 500);
   }
